import {
    fetch
} from 'utils/fetch';

export function listBasic(page, rows) {
    return fetch({
        url: '/set/list.do',
        // url: 'http://10.172.20.30:8080/set/list.do',
        method: 'get',
        params: {
            page,
            rows
        }
    });
}

export function updateBasicCredit(onedayScoreLimit, id) {
    return fetch({
        url: '/set/update.do',
        // url: 'http://10.172.20.30:8080/set/update.do',
        method: 'post',
        data: {
            id,
            onedayScoreLimit
        }
    });
}

export function listBusiness(pageNo, pageSize) {
    return fetch({
        url: '/bus/list.do',
        // url: 'http://10.172.30.107:8093/bus/list.do',
        method: 'get',
        params: {
            pageNo,
            pageSize
        }
    });
}

export function listSign(pageNo, pageSize) {
    return fetch({
        url: '/bus/sign/historyList.do',
        // url: 'http://10.172.30.107:8093/bus/sign/historyList.do',
        method: 'get',
        params: {
            pageNo,
            pageSize
        }
    });
}


export function updateCreditSign(onedayScoreLimit, score, memo) {
    return fetch({
        url: '/bus/sign/modify.do',
        // url: 'http://10.172.30.107:8093/bus/sign/modify.do',
        method: 'post',
        data: {
            score,
            totalScoreLimit: onedayScoreLimit,
            memo

        }
    });
}

export function getcurrentSign() {
    return fetch({
        url: '/bus/sign/currentSign.do',
        // url: 'http://10.172.30.107:8093/bus/sign/currentSign.do',
        method: 'get'
    });
}

export function updateBCredit(busId, busName, link, memo) {
    return fetch({
        url: '/bus/save.do',
        // url: 'http://10.172.30.107:8093/bus/save.do',
        method: 'post',
        data: {
            busId,
            busName,
            link,
            memo
        }
    });
}


