<template>
    <div class="reset-container">
        <el-form autoComplete="on" label-position="left"
                 label-width="0px" :rules="rules" :model="bForm" ref="bForm"
                 class="card-box reset-form">
            <div>
                <router-link to="/business-credit" class="back-icon">
                    首页<i class="el-icon-arrow-right"></i>业务信用分<i class="el-icon-arrow-right"></i>新增业务
                </router-link>
            </div>
            <el-form-item prop="bName">
                <span class="title">业务名称</span><el-input name="bName" v-model="bForm.bName" type="text" placeholder="业务名称"></el-input>
            </el-form-item>
            <el-form-item prop="bHref">
                <span class="title">跳转链接</span><el-input name="bHref" v-model="bForm.bHref" placeholder="跳转链接"></el-input> 请填写完整url链接
            </el-form-item>
            <el-form-item prop="bMemo">
                <span class="title">备注</span><el-input name="bMemo" v-model="bForm.bMemo" placeholder="备注"></el-input>
            </el-form-item>            
            <!--<el-form-item prop="code">
                <span class="title">任务上限总分</span>
                <span class="choose">
                <el-radio v-model="missionMax" label="2">无上限</el-radio>
                </span>
                <span class="choose">
                <el-radio v-model="missionMax" label="1">{{emptyctxt}}</el-radio><el-input name="code" type="number" :disabled="missionMax==2" v-model="missionMaxInput" placeholder="任务上限总分" @input="validMission"></el-input>
                </span>
            </el-form-item>-->
            <el-form-item style="width:100%;">
                <el-button type="primary" style="width:80px" @click="updatedMax('bForm')">
                    提交
                </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import {
        updateBCredit
    } from 'api/basicCredit';

    export default {
        name: 'reset',
        data() {
            return {
                bForm: {
                    bName: (this.$route.query.busName || this.$route.query.busName) != "null" ? this.$route.query.busName : "",
                    bHref: (this.$route.query.link || this.$route.query.link) != "null" ? this.$route.query.link : "",
                    bMemo: (this.$route.query.memo || this.$route.query.memo) != "null" ? this.$route.query.memo : ""
                },
                rules: {
                    bName: [{
                        required: true,
                        message: '请输入业务名称'
                    }]
                }
            }
        },
        methods: {
            updatedMax(formName) {
                var _vue = this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        updateBCredit(this.$route.query.busId ? this.$route.query.busId : "", _vue.bForm.bName, _vue.bForm.bHref, _vue.bForm.bMemo).then(response => {
                            if (response.success) {
                                _vue.open();
                            } else {
                                _vue.open(response.data);
                            }
                            resolve();
                        }).catch(error => {});
                    } else {
                        return false;
                    }
                });
            },
            open(info) {
                var _vue = this;
                this.$alert(info ? info : '操作成功', '提示信息', {
                    confirmButtonText: '确定',
                    callback: action => {
                        _vue.$router.push('/business-credit');
                    }
                });
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";
    .reset-container {
        .el-form-item {
            float: left;
            width: 100%;
            margin-top: 20px;
            span {
                width: 80px;
                float: left;
            }
            .el-input {
                width: 60%;
                float: left;
            }
        }
        .el-form-item__content {
            float: left;
        }
        input:-webkit-autofill {
            -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
            -webkit-text-fill-color: #3E3E3E !important;
        }
        @include relative;
        height: 100vh;
        background-color: #324057;
        .back-icon {
            float: left;
            margin-top: 5px;
        }
        .el-icon-information {
            position: absolute;
            right: -18px;
            top: 10px;
        }
        .reset-form {
            position: absolute;
            left: 0;
            right: 0;
            width: 350px;
            padding: 35px 35px 15px 35px;
            margin: 120px auto;
        }
        .card-box {
            padding: 20px;
            box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
            -webkit-border-radius: 5px;
            border-radius: 5px;
            -moz-border-radius: 5px;
            background-clip: padding-box;
            margin-bottom: 20px;
            background-color: #F9FAFC;
            width: 580px;
            border: 2px solid #8492A6;
        }
        .title {
            text-align: center;
            color: #505458;
        }
    }
</style>


